<template>
<div id="page-content-wrapper">
	<div class="card mb-3">
          <div class="card-header">
            <h2>Data Employee</h2>
            </div>
          <div class="card-body">
            <div class="input-group flex-nowrap">
            <div class="input-group-prepend">
              <span class="input-group-text" id="addon-wrapping">Search</span>
            </div>
            <input type="text" 
              class="form-control" 
              placeholder="Search on this" 
              aria-label="search" 
              aria-describedby="addon-wrapping"
              v-model="searchName"
              @keyup.enter="search">
          </div><br>
            <router-link
              tag="button" 
              style="text-align: left; margin-bottom: 10px" 
              class="btn btn-secondary btn-lg btn-block"
              to="/employee/new">Create Employee</router-link>
            <div class="table-responsive">
              <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                <thead>
                  <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>Age</th>
                    <th>Address</th>
                    <th>Gender</th>
                    <th>Avatar</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tfoot>
                  <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>Age</th>
                    <th>Address</th>
                    <th>Gender</th>
                    <th>Avatar</th>
                    <th></th>
                  </tr>
                </tfoot>
                <tbody>
                  <tr v-for="employee in employees" :key="employee.id">
                    <td class = "demo">{{employee.id}}</td>
                    <td>{{employee.name}}</td>
                    <td>{{employee.age}}</td>
                    <td>{{employee.address}}</td>
                    <td>{{employee.gender == true ? 'Male':'Female'}}</td>
                    <td><img :src="employee.avatar" class="img-fluid"/></td>
                    <td>
                      <button type="button" class= "btn btn-danger" @click="deleteEmployee(employee.id)">Delete</button>
                      <router-link
                        tag="button" 
                        class="btn btn-primary"
                        :to="'/employee/'+employee.id">View</router-link>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <Pagniation></Pagniation>
        </div>
        </div>
</template>
<script>
import axios from '../../axios-auth'
import Pagniation from './Pagniation'
export default {
	data() {
    return {
      page: this.$route.query.page,
      name: this.$route.query.name,
      employees: [],
      searchName: ''
    }
  },
  components:{
    Pagniation
  },
  created(){
    document.title = "Employee Table"
    this.getEmployees()
  },
  methods: {
      getEmployees(){
      if(isNaN(this.page)){
        this.page=1
      }
		axios.get('/Personal?page='+this.page+'&limit=10')
			.then(res =>{
				const data = res.data
				const employees = []
				for(let key in data){
					const employee = data[key]
					employees.push(employee)
        }
        this.employees = employees
      })
    },
    deleteEmployee(empId){
      axios.delete('/Personal/'+empId)
      .then(res => {
        console.log(res)
        alert("Delete Successfully!!!!")
        this.getEmployees()
      })
      .catch(error => console.log(error))
    },
    search(){
      const url = '/Personal?search='+this.searchName
      axios.get(url)
      .then(res =>{
      })
    }
  }
}
</script>
<style scoped>
    @import '../../assets/vendor/fontawesome-free/css/all.min.css';
    @import '../../assets/vendor/datatables/dataTables.bootstrap4.css';
    @import '../../assets/css/sb-admin.css';

</style>


