<template>
<div id="page-content-wrapper">
  <div class="card-header">
    <h2>Employee {{id}} Detail</h2>
  </div>
  <form @submit.prevent="onSubmit">
  <img v-if="id!='new'" :src="employee.avatar" class="img-fluid"/>
  <div class="form-group">
    <label for="eName">Employee's Name</label>
    <!-- <input type="text" class="form-control" id="eName"  v-model.trim = "$v.eName.$model" aria-describedby="emailHelp"> -->
    <input type="text" class="form-control" id="eName"  v-model = "eName" @input = "$v.eName.$touch()" aria-describedby="emailHelp">
    <div class="error" v-if="!$v.eName.required">Name is required</div>
    <div class="error" v-if="!$v.eName.minLength">Name must be at least {{$v.eName.$params.minLength.min}} characters</div>
  </div>

  <div class="form-group">
    <label for="eAge">Employee's Age</label>
    <input type="number" class="form-control" v-model.trim = "$v.eAge.$model" id="eAge">
    <div class="error" v-if="!$v.eAge.between">Age must be between 18 and 55</div>
  </div>
  <div class="form-group form-check">
    <input type="checkbox" v-model="eGender" value = "true" class="form-check-input" id="eGender">
    <label class="form-check-label" for="eGender">Is Male?</label>
  </div>
  <div class="form-group">
    <label for="eAddress">Employee's Address</label>
    <input type="text" class="form-control" id="eAddress" v-model.trim="$v.eAddress.$model">
    <div class="error" v-if="!$v.eAddress.required">Address is required</div>
  </div>
  <button type="button" @click = "saveEmployee" class="btn btn-primary">{{id == 'new' ? 'Create':'Update'}}</button>
</form>
</div>
</template>
<script>
import axios from '../../../axios-auth'
import { required, minLength, between } from 'vuelidate/lib/validators'
export default {
  data(){
    return {
      id: this.$route.params.id,
      submit: false,
      employee: {
        id: Number,
        name: String,
        age: Number,
        address: String,
        gender: false,
        avatar: String
      },
      eName:'',
      eAge:'',
      eGender:false,
      eAddress:'',
    }
  },
  validations: {
    eName: {
      required,
      minLength: minLength(4)
    },
    eAge: {
      between: between(18, 55)
    },
    eAddress: {
      required,
    }
  },
  methods: {
    getEmployee(){
      if(!isNaN(this.id)){
      axios.get('/Personal/'+this.id)
		    .then(res =>{
          this.employee = res.data;
          console.log(this.employee)
          this.id = this.employee.id;
          this.eName = this.employee.name
          this.eAge = this.employee.age
          this.eGender = this.employee.gender
          this.eAddress = this.employee.address
          })
        .catch(error => console.log(error))
      }
    },
    saveEmployee(){
      this.employee.name = this.eName
      this.employee.age = this.eAge
      this.employee.address = this.eAddress
      this.employee.gender = this.eGender
      if(!isNaN(this.id)){
        this.updateEmployee()  
      }else{
        this.createEmployee()
      }
      this.getEmployee()
    },
    updateEmployee(){
      this.employee.id = this.id
      const url = "/Personal/"+this.employee.id
      axios.put(url, this.employee)
      .then(res => {
        console.log(res)
        alert(" Update Employee Information Successfully!")
      })
      .catch(error => console.log(error))
    },
    createEmployee(){
      const url = "/Personal"
      axios.post(url,this.employee)
      .then(res => {
        alert("Create Successfully!")
      })
      .catch(error => console.log(erro))
    }
  },
	created(){
    this.getEmployee()
    document.title = 'Employee Detail'
  }
}
</script>
<style scoped>
	#page-content-wrapper {
    text-align: left;
    padding: 0 20px 0 30px
  }
  .card-header {
    text-align: left;
    margin-bottom: 20px
  }
  .error {
    margin-top: 5px;
    color: red;
    font-style: italic;
  }
  .img-fluid{
    padding-bottom: 20px
  }
</style>


