<template>
	<nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
            <li v-for = "number in pagniation" :key = "number" class="page-item">
            <button @click="goToPage(number)" class="page-link" :class="number==page?'active':''">{{number}}</button>
            </li>
        </ul>
    </nav>	
</template>
<script>
import axios from '../../axios-auth'
export default {
	data(){
		return {
			page: this.$route.query.page,
      		pagniation: 0
		}
	},
	created(){
		this.totalEmploye()
	},
	methods: {
		totalEmploye(){
			axios.get('/Personal')
					.then(res =>{
			const data = res.data
			const total = data.length
			if(total%10!=0){
				this.pagniation = Math.ceil(total/10)
				console.log(this.pagniation)
				}
			})
		},
		goToPage(selectedPage){
      		this.$router.push('/employee?page='+selectedPage);
    	}
	}
}
</script>
<style scoped>

</style>


