import Vue from 'vue'
import VueRouter from 'vue-router'

import DefaulPage from '@/components/DefaultPage'
import Employee from '@/components/pagecontent/Table'
import Dashboard from '@/components/pagecontent/Dashboard'
import FormEmployee from '@/components/pagecontent/employee/FormEmployee'
Vue.use(VueRouter)

const routes = [
    { path: '/', component: DefaulPage, children:[
        { path: '', component: Dashboard},
        { path: 'employee', component: Employee},
        { path: 'employee?page=:page', component: Employee, props: (route)=>({page: route.query.page})},
        { path: 'employee?search=:name', component: Employee, props: (route)=> ({name: route.query.name})},
        { path: 'employee/:id', component: FormEmployee }
    ] },
    { path: '*', component: DefaulPage}
]
export default new VueRouter({mode: 'history', routes})


